import { useAuth } from "../context/AuthContext";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";

const Dashboard = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const [projects, setProjects] = useState([]);

  // Load projects from localStorage
  useEffect(() => {
    const stored = JSON.parse(localStorage.getItem("projects")) || [];
    setProjects(stored);
  }, []);

  // Handle project deletion
  const deleteProject = (id) => {
    if (window.confirm("Delete this project?")) {
      const updated = projects.filter((p) => p.id !== id);
      setProjects(updated);
      localStorage.setItem("projects", JSON.stringify(updated));
    }
  };

  return (
    <div className="p-6 relative min-h-screen bg-gray-50">
      {/* Top-right user info */}
      <div className="absolute top-4 right-6 flex items-center gap-4">
        <div className="flex items-center gap-2">
          <img
            src={user.photoURL}
            alt="User"
            className="w-10 h-10 rounded-full border"
          />
          <div className="text-right">
            <p className="font-semibold">{user.displayName}</p>
            <p className="text-xs text-gray-500">{user.email}</p>
          </div>
        </div>
        <button
          onClick={logout}
          className="bg-red-500 text-white px-3 py-1 rounded-md text-sm hover:bg-red-600 transition"
        >
          Logout
        </button>
      </div>

      {/* Main content */}
      <div className="mt-20">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-bold">Your Projects</h2>
          <button
            onClick={() => navigate("/project")}
            className="bg-green-600 hover:bg-green-700 text-white px-6 py-2 rounded-lg shadow transition duration-200"
          >
            + Add Project
          </button>
        </div>

        <div className="flex overflow-x-auto space-x-4 pb-4">
          {projects.length === 0 ? (
            <p className="text-gray-500">No projects yet. Add one!</p>
          ) : (
            projects.map((project) => (
              <div
                key={project.id}
                className="min-w-[280px] bg-white shadow-md rounded-lg p-4 border flex-shrink-0"
              >
                <h3 className="font-semibold text-lg">{project.title}</h3>
                <p className="text-sm text-gray-600 mt-1">{project.description}</p>
                <div className="flex justify-between mt-4">
                  <button
                    onClick={() => navigate(`/project/${project.id}`)}
                    className="text-blue-600 hover:underline"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => deleteProject(project.id)}
                    className="text-red-600 hover:underline"
                  >
                    Delete
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
